*, :before, :after {
  box-sizing: border-box;
  font-family: Gotham Rounded, sans-serif;
  font-weight: normal;
}

body {
  height: 100vh;
  background: linear-gradient(to right, #810383, #7b4b8776);
}

#ProfileInfo {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5vh;
  display: flex;
}

#bioID {
  width: 500px;
  height: 250px;
  word-wrap: break-word;
  color: #fff;
  text-shadow: .8px .8px .8px #ca40d4;
  background-color: #a566b3;
  border: 3px solid #5d3268;
  border-radius: 20px;
  margin-top: 5vh;
  padding: 10px;
  overflow-y: scroll;
  box-shadow: 4.4px 4.4px 4.4px #d341f0;
}

#genderID {
  margin-top: 5vh;
}

